import React from 'react';
import BlogTemplate from './BlogTemplate'
import Main from 'layouts/SP2P';
import WithLayout from 'WithLayout';
import { graphql } from 'gatsby'

const BlogTemplateWrapper = (data) => {
  return (
    <WithLayout
      component={BlogTemplate}
      layout={Main}
      data={data}
    />
  )
};

export default BlogTemplateWrapper;

export const pageQuery = graphql`
  query BlogPostByPath($path: String!, $category: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        author
        cover
        tags
        subtitle
      }
    },
    allMarkdownRemark(filter: { frontmatter: {category: { eq: $category }, path: { ne: $path }}}, limit: 4, sort: {order: DESC, fields: [frontmatter___date]}) {
        edges {
            node {
                excerpt(pruneLength: 250)
                id
                frontmatter {
                    title
                    subtitle
                    date(formatString: "MMMM DD, YYYY")
                    path
                    tags
                    author
                    cover
                }
            }
        }
    }
  }`
