import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';
import { Section, SectionAlternate } from 'components/organisms';
import {
    Content,
    FooterNewsletter,
    Hero,
    SidebarArticles,
    SidebarNewsletter,
    SimilarStories,
} from './components';

import { Helmet } from 'react-helmet';
import { simplifyEdges } from 'utils/graphUtils';

const useStyles = makeStyles(theme => ({
    root: {
        height: '100%',
        width: '100%',
    },
    sidebarNewsletter: {
        marginTop: theme.spacing(4),
        [theme.breakpoints.down('sm')]: {
            marginTop: theme.spacing(2),
        },
    },
    footerNewsletterSection: {
        background: theme.palette.primary.dark,
    },
}));

const BlogTemplate = ({ data }) => {
    const classes = useStyles();
    const { markdownRemark: { frontmatter: postData, html: postHtml }, ...etc } = data;
    const similarPosts = simplifyEdges(etc);

    return (
        <div className={classes.root}>
            <Helmet title={postData.title} />
            <Hero
                cover={postData.cover}
                title={postData.title}
                subtitle={postData.subtitle}
                author={postData.author}
                date={postData.date}
            />
            <Section>
                <Grid container spacing={4}>
                    <Grid item xs={12} md={8}>
                        <Content data={postData} html={postHtml} />
                    </Grid>
                    <Grid item xs={12} md={4}>
                        {similarPosts.length > 0 && (<SidebarArticles data={similarPosts} />)}
                        <SidebarNewsletter className={classes.sidebarNewsletter} />
                    </Grid>
                </Grid>
            </Section>
            <SectionAlternate>
                {similarPosts.length > 0 && (<SimilarStories data={similarPosts} />)}
            </SectionAlternate>
            <SectionAlternate className={classes.footerNewsletterSection}>
                <FooterNewsletter />
            </SectionAlternate>
        </div>
    );
};

export default BlogTemplate;
